<template>
	<div class="home">
		<banner :data="banner" collectionName="banner"></banner>
		<div class="box">
			<uc-cell
			v-for="(item,index) of list"
			:key='item._id'
			:data="item" 
			:index='index' 
			collectionName='home'
			></uc-cell>
		</div>
		
	</div>
</template>

<script>
	import banner from '../components/uc-banner/uc-banner.vue'
	import UcCell from '../components/uc-cell/uc-cell.vue'
	import axios from 'axios';
	
	export default {
		name: 'home',
		props: {},
		data() {
			return {
				banner: [],
				list:[]
			}
		},
		components: {banner,UcCell},
		mounted() {
			axios({
				url:'/api/home',
				params:{_limit:20,_page:1}
				// headers:{token:'1234567890123456'}    //这里token在拦截器里已经携带了(不用每次都带了)
			}).then((res)=>{
				// console.log(res);
				this.list = res.data.data
			})
			
			axios({
				url:'/api/banner',
				params:{_limit:3,_page:1}
			}).then((res)=>{
				// console.log(res);
				this.banner = res.data.data
			})
		},
		updated() {},
		methods: {}
	}
</script>

<style scoped>
	.home {}
	.box{
		padding: 0 0.35rem;
		padding-bottom: 0.8rem;
	}
</style>
