<template>
	<div class="column">
		<uc-cell
		v-for="(item,index) of list"
		:key='item._id'
		:data="item" 
		collectionName='hotspot'
		></uc-cell>
	</div>
</template>

<script>
	import UcCell from '../components/uc-cell/uc-cell.vue'
	export default {
		name:'hotspot',
		props:{},
		data(){
			return{
				list:[]
			}
		},
		components:{UcCell},
		mounted(){
			this.$axios({
			  url:'/api/hotspot',
			  params:{_limit:15,_page:1}
			}).then((res)=>{
				// console.log(res);
				this.list = res.data.data
			})
		},
		updated(){},
		methods:{}
	}
</script>

<style scoped>
	.column{
		padding: 0.7rem 0.35rem;
	}
</style>
