import Vue from 'vue';
import VueRouter from 'vue-router';
Vue.use(VueRouter);

import Home from '../pages/home.vue'
import Follow from '../pages/follow.vue'
import HotSpot from '../pages/hotspot.vue'
import Detail from '../pages/detail.vue'
import Login from '../pages/login.vue'
import Reg from '../pages/reg.vue'
import User from '../pages/user.vue'
import NoPage from '../layouts/NoPage.vue'

let routes = [
  {path:'/home',component:Home},
  {path:'/follow',component:Follow},
  {path:'/hotspot',component:HotSpot},
  /* {path:'/detail',component:Detail,children:[   //这样写,页面就会在二级展示区中显示
	{path:':id',component:Detail}
  ]}, */
  {path:'/detail/:_id',component:Detail,name:'detail'},   //简写(两个访问的组件相同,可以这样写)  这样只会在一级显示区显示
  {path:'/login',component:Login},
  {path:'/reg',component:Reg},
  {path:'/user',component:User},
  {path:'/',redirect:'/home'},
  {path:'*',component:NoPage},
];

let router = new VueRouter({
  routes
});

export default router;