<template>
	<div class="banner">
		<ul class="clearfix">
		  <router-link
		    tag="li"
		    v-for="(item, index) of data" 
		    :key="item._id"
		    :to="{name:'detail',params:{_id:item._id},query:{collectionName}}"
		  >
		    <img :src="item.banner" alt="" />
		    <div class="text-box">
		      <h2>{{ item.title }}</h2>
		      <p>{{ item.sub_title }}</p>
		    </div>
		  </router-link>
		</ul>

	</div>
</template>

<script>
	import Swipe from './js/swipe.js';
	import './js/jquery-1.7.2.js';
	
	
	export default {
		name:'banner',
		props:{
			data:{   //类似于: [{_id:xx,image:xx,title:xx,sub_title:xx}]
				type:Array,
				required:true
			},
			collectionName:{  //根据_id跳转到详情页,需要一个集合(就是这张图片点进去后的信息)
				type:String,
				required:true
			}
		},
		data(){
			return{
				
			}
		},
		components:{},
		mounted(){    //数据长度不为0,则调用方法
			this.data.length != 0 && this.initSwiper() 
		},
		updated(){
			this.initSwiper()
		},
		methods:{
			initSwiper(){
			  new Swipe($('.banner')[0], {
			    auto: 2000,
			    continuous: true,
			    stopPropation: true,
			    callback: function(index, element) {
			      $('.banner ol li').removeClass('active');
			      $('.banner ol li')
			        .eq(index)
			        .addClass('active');
			    }
			  });
			}
		}
	}
</script>

<style scoped>
.banner{overflow: hidden;}
.banner ul li {
  position: relative;
  overflow: hidden;
  z-index: 1;
  width: 6.4rem;
  float: left;
}
.banner ul li img {
  width: 100%;
  display: block;
}
.banner ul li .text-box {
  width: 5.8rem;
  padding: 0 0.3rem;
  height: 1.24rem;
  position: absolute;
  left: 0;
  bottom: 0;
  color: #fff;
  background: rgba(0, 0, 0, 0.5);
}
.text-box h2 {
  font-size: 0.3rem;
  font-weight: normal;
  margin-top: 0.22rem;
  line-height: 100%;
  margin-bottom: 0.22rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.text-box p {
  line-height: 100%;
}

.banner ol {
  position: absolute;
  right: 0.2rem;
  bottom: 0.2rem;
}
.banner ol li {
  width: 0.15rem;
  height: 0.15rem;
  background: #5477b2;
  float: left;
  border-radius: 50%;
  margin-right: 0.08rem;
}
.banner ol li.active {
  background: #fff;
}
</style>
