<template>
	<div class="uc-cell">
		<div @click="toDetail">
			<h2><span v-show="index != null">{{index+1}}.</span>{{data.title}}</h2>
			<p>{{data.des}}</p>
			<a class="cell__anchor" href="javascript:;" v-if="isAdd" @click.stop="$emit('add-item',$event)">+</a>
		</div>															<!-- 修饰符:stop :阻止冒泡事件 -->				
	</div>
</template>

<script>
	export default {
		name:'uc-cell',
		props:{
			data:{    //类似于: {_id:xx,title:xx,des:oo}
				type:Object,
				required:true
			},
			collectionName:{
				type:String,
				default:null
			},
			index:{
				type:Number,
				default:null
			},
			isAdd:{    //是否添加收藏
				type:Boolean,
				default:false
			}
		},
		components:{},
		mounted(){},
		updated(){},
		methods:{
			toDetail(){   //判断若是传递了collectionName,就跳转详情页
				if(this.collectionName){
					this.$router.push(`/detail/${this.data._id}?collectionName=${this.collectionName}`)
				}
			}
		}
	}
</script>

<style scoped>
.uc-cell{position: relative;}
.uc-cell:active {background: #ccc;}
.uc-cell div { color:#494d4d; padding:0.2rem 0; border-bottom:1px dashed #ccc;}
.uc-cell div h2{max-height:0.9rem; font-size:0.33rem; overflow:hidden;}
.uc-cell div p{max-height:1.8rem; margin-top:0.05rem;overflow:hidden;}
.cell__anchor{position:absolute;right:0.3rem;top:0.3rem;font-size: 0.4rem;}
</style>
