<template>
	<div class="follow">
			<uc-cell
			v-for="(item,index) of list"
			:key='item._id'
			:data="item" 
			:index='index' 
			collectionName='follow'
			></uc-cell>
	</div>
</template>

<script>
	import UcCell from '../components/uc-cell/uc-cell.vue'
	export default {
		name:'follow',
		props:{},
		data(){
			return{
				list:[]
			}
		},
		components:{UcCell},
		mounted(){
			this.$axios({
			  url:'/api/follow',
			  params:{_limit:10,_page:1}
			}).then((res)=>{
				// console.log(res);
				this.list = res.data.data
			})
		},
		updated(){},
		methods:{}
	}
</script>

<style scoped>
	.follow{
		padding: 0.7rem 0.35rem;
	}
</style>
