import Vue from 'vue'
import App from './layouts/App.vue'

Vue.config.productionTip = false

//一个模块如何引入,完全取决于它是如何导出的
//这里的话,文件没有暴露,所以直接引入文件就行了
import './assets/css/base.css'   //相当于使用 src 引入
import './assets/js/font.js'     //字体比值的控制

//引入路由配置
import router from './plugins/router.js'
//引入拦截器
import './plugins/axios'    //将axios放到全局上
//引入过滤器
import './filters/index.js'


let vm = new Vue({
  render: h => h(App),
  router,
  data:{
	bNav:false,
	bFoot:false,
	bLoading:true
  }
}).$mount('#app')

//暴露出vm实例
export default vm;