import axios from 'axios';
import Vue from 'vue';
import router from './router.js'
import vm from '../main.js'

//添加一个请求的拦截器 (劫持)
axios.interceptors.request.use(function(config) {
  //config 含有发出请求时的配置信息  axios(config)
  
  // 每次都自动携带token  (在拦截器中处理axios,不用我们在每次发送axios请求时携带了)
  let token = window.localStorage.getItem('user')
  token = token ? JSON.parse(window.localStorage.getItem('user')).token : ''
  config.headers={
    token: token
  }
  
  //显示loading
  vm.bLoading = true;
  
  return config;// 将我们处理过的请求,再放出去
}, function(error) {
  // 发出了错误的请求，直接拦截
  return Promise.reject(error);
});

// 添加一个响应的拦截器
axios.interceptors.response.use(function(response) {
  //response  就是 axios请求后的res
  // 响应数据回来后，到达目标组件之前，做点事   res.status   res.data.err == 2
  //校验返回数据，token过期，路由跳转login,传递当前路由地址
  let currentRoute = router.currentRoute.fullPath;//router中有个API,为currentRoute :获取当前(地址栏)路由全路径，是个string,里面又有个属性叫fullPath
  if(response.data.err===2 && !currentRoute.includes('/login')){    //若响应时返回的err是2,且路径中没有匹配到'/login'
    
    router.replace({    //则路由替换为:登录的路由,转到登录页面
      path:'/login',
      query:{p:currentRoute}    //我在访问关注页面,但没登录,等我登录后,还要回到之前访问的那个关注页面 (拿到之前访问时携带的查询字符串)
    })  //将这段查询字符串传给登录的路由
  }
  
  //隐藏loading
  vm.bLoading = false;
  
  return response;//将响应的数据返回给组件
}, function(error) {
  // 错误的响应，直接拦截
  return Promise.reject(error);
});


Vue.prototype.$axios=axios;
// window.axios=axios;  ×
export default axios;